.header{height:100px}.header .navbar-brand img{width:50px}.protectedNav .container-fluid{-webkit-justify-content:flex-start;justify-content:flex-start}.protectedNav .container-fluid .btn{font-size:1rem;color:#2F2F2F;font-weight:500}.protectedNav .container-fluid .btn.logout{margin-left:auto}@media only screen and (min-width: 375px){.navbar{padding:.5rem 0rem}}

.homePage{width:95%;margin:4rem auto;text-align:center;-webkit-justify-content:center;justify-content:center;color:#293446}.homePage_header{width:80%;margin:0 auto 4rem;white-space:nowrap}.homePage_header h1{font-size:1rem}@media only screen and (min-width: 375px){.homePage_header h1{font-size:18px}}@media only screen and (min-width: 768px){.homePage_header h1{font-size:30px}}@media only screen and (min-width: 992px){.homePage_header h1{font-size:44px}}@media only screen and (min-width: 1200px){.homePage_header h1{font-size:64px}}


.product{max-width:20%;min-width:280px;margin-bottom:3rem;position:relative}.product .pencil{position:absolute;top:-13px;right:0;width:2rem;height:2rem;background-color:black;border-radius:100rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:18px;cursor:pointer}.product .pencil i{font-size:15px;color:white}.product img{width:100%;height:240px;object-fit:cover;margin-bottom:10px}.product .title{font-size:16px;font-weight:500;margin-bottom:8px}.product .price{color:rgba(0,0,0,0.6);font-size:14px;margin-bottom:5px}.product .btn.btn-dark{font-size:14px}.product .btn.btn-dark.copy{width:100%;margin-right:15px}.product .btn.btn-dark.arrow{padding:0 1rem}.product .links{max-height:0;transition:all .3s ease-in;overflow:hidden}.product .links.show{max-height:500px}.addToFav{cursor:pointer;font-size:1.4rem}

.products_list{padding:3rem}.filterProperties{padding:4rem 3rem 0}.input-range__slider{-webkit-appearance:none;appearance:none;background:#3f51b5;border:1px solid #3f51b5;border-radius:100%;cursor:pointer;display:block;height:1rem;margin-left:-0.5rem;margin-top:-0.65rem;outline:none;position:absolute;top:50%;transition:box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;transition:transform 0.3s ease-out, box-shadow 0.3s ease-out;transition:transform 0.3s ease-out, box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;width:1rem}.input-range__slider:active{-webkit-transform:scale(1.3);transform:scale(1.3)}.input-range__slider:focus{box-shadow:0 0 0 5px rgba(63,81,181,0.2)}.input-range--disabled .input-range__slider{background:#cccccc;border:1px solid #cccccc;box-shadow:none;-webkit-transform:none;transform:none}.input-range__slider-container{transition:left 0.3s ease-out}.input-range__label{color:#aaaaaa;font-family:"Helvetica Neue", san-serif;font-size:0.8rem;-webkit-transform:translateZ(0);transform:translateZ(0);white-space:nowrap}.input-range__label--min,.input-range__label--max{bottom:-1.4rem;position:absolute}.input-range__label--min{left:0}.input-range__label--max{right:0}.input-range__label--value{position:absolute;top:-1.8rem}.input-range__label-container{left:-50%;position:relative}.input-range__label--max .input-range__label-container{left:50%}.input-range__track{background:#eeeeee;border-radius:0.3rem;cursor:pointer;display:block;height:0.3rem;position:relative;transition:left 0.3s ease-out, width 0.3s ease-out}.input-range--disabled .input-range__track{background:#eeeeee}.input-range__track--background{left:0;margin-top:-0.15rem;position:absolute;right:0;top:50%}.input-range__track--active{background:#3f51b5}.input-range{height:1rem;position:relative;width:100%}

.abs{position:absolute;top:1rem;right:1rem;z-index:999999}.fa-heart-o,.fa-heart{color:#5890DB}

*{font-family:'Poppins', sans-serif}body{width:100%;overflow-x:hidden}p{font-size:14px;color:rgba(0,0,0,0.8);font-weight:600;margin-bottom:0}.h1,.h2,.h3,.h4,.h5,.h6,h1,h2,h3,h4,h5,h6{margin-bottom:0}label.required::after{content:'*';margin-left:4px;color:#FF0000;opacity:.8}label.required+span,span.required{font-size:12px;margin-top:8px;float:right}label{font-size:14px;color:#000000;opacity:.8;font-weight:500}.cocialMediaLinking .progress-bar{background-color:#000000;border-radius:4px;padding:.5rem .8rem;text-align:unset;-webkit-justify-content:unset;justify-content:unset;color:#FFFFFF;font-size:13px;font-weight:600;margin-bottom:1rem}.btn.btn-primary{background-color:#3490DC;border-radius:4px;font-size:14px;font-weight:400;padding:7px 3rem;position:relative;z-index:999}@media only screen and (min-width: 375px){.btn.btn-primary{padding:7px 1rem}}.btn-check:active+.btn-primary:focus,.btn-check:checked+.btn-primary:focus,.btn-primary.active:focus,.btn-primary:active:focus,.show>.btn-primary.dropdown-toggle:focus{box-shadow:unset !important}.inputLabel_container input{max-width:400px}.custom-checkbox{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;cursor:pointer}.custom-checkbox label{cursor:pointer}.custom-checkbox input{margin-right:.5rem;cursor:pointer}.addProduct{margin:4rem;position:relative}.back_btn{position:absolute;top:0;left:1rem;font-size:18px;font-weight:500;text-decoration:underline;background-color:transparent;border:unset;cursor:pointer;text-transform:capitalize}


